body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  background-color: rgba(247, 227, 192, 0.727);
  margin: 15px auto 100px auto;
  padding-bottom:30px; 
}
h2 {
  text-align: center;
  color: black;
  font-weight: bold;
}

#carryout, #homedelivery {
  text-align: center;
  color: red;
  font-weight: bold;
}

#submit{
  float: center;
}

#home, #menu, #chkout{
  color: white;
}


#signup, #login{
  color:white;
  float:right;

}

#footer {
  background-color:gray;
  height:50px;
  color:black;
  text-align:center;
  padding-top:5px;
  bottom:0;
  position: relative;;
  width:100%;
}


.Navbar{
    padding: 5px;
    background-color: rgba(32, 32, 32, 0.7);
}


.brand{
    font-size:32px;
    margin-left:20px;
    float: left;
    text-decoration: none;
}   
.brand-logo{
    width: 50px;
    height: 50px;
    float: left;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Countdown{
  margin: 10px auto;
  padding-bottom: 20px;
  text-align: center;
}

.Countdown-col{
  display: inline-block;
}

.Countdown-col-element{
  display: inline-block;
  margin: 0 20px;
  display: flex;
  flex-direction: column;
}

.Countdown-col-element strong{
  font-size: 50px;
}


